<template>
	<div class="container">
		<div class="row">
		<div class="col-md-7 col-md-offset-2" >

			<h3 class="page-header">
			<router-link :to="{ name: 'home'}" name="home"> HOME </router-link> <  Map of Survivors</h3>
			<div class="form-group row">
				<div class="col-md-6">
					<input name="noninfected" type="checkbox" v-model="selection.one" checked> Show survivor non infected
				</div>
				<div class="col-md-6">
					<input name="infected" type="checkbox" v-model="selection.two"> Show survivor infected
				</div>
			</div>
			<div class="well">
			<gmap-map
				:center="center"
				:zoom="6"
				style="width: 100%; height: 300px">
						<div v-if="selection.one === true">
							<gmap-marker
								icon="https://github.com/IgorVieira/igorvieira.github.io/blob/master/_images/clean.png?raw=true"
								:key="index"
								v-for="(m, index) in markersN"
								:position="m.lastLocation">
							</gmap-marker>
						</div>
						<div v-if=" selection.two === true">
							<gmap-marker
								icon="https://github.com/IgorVieira/igorvieira.github.io/blob/master/_images/infected.png?raw=true"
								:key="index"
								v-for="(m, index) in markersI"
								:position="m.lastLocation">
							</gmap-marker>
						</div>
			</gmap-map>
			</div>
		</div>
	</div>
</div>
</template>
<script>
/* eslint-disable no-undef  */
/* eslint-disable no-underscore-dangle */
import _ from 'lodash';
import SurvivorService from '../../domain/survivor/SurvivorService';

export default {

	data() {
		return {
			center: { lat: -19.8934596, lng: -44.0586543 },
			selection: {
				one: true,
				two: false,
			},
			markersN: [],
			markersI: [],
		};
	},
	created() {
		const vm = this;
		vm.service = new SurvivorService(vm.$resource);

		vm.service
				.listSurvivor()
				.then((res) => {
					_.map(res, (pers) => {
						if (pers.infected === true) vm.markersI.push(pers);
						else vm.markersN.push(pers);
					});
				})
				.catch((err) => {
					toastr.error(`Error in load list: ${err}`, 'Status');
				});
	},
};
</script>
<style>
</style>



