<template>
    <div class="container">
  		<div class="row">
            <div class="col-md-7 col-md-offset-2" >

                <h3 class="page-header">
                <router-link :to="{ name: 'home'}"> HOME </router-link> <  Check Survivors Survivor</h3>
                <form  @submit.prevent="checkIdForTrade()">
                    <div class="form-group row">
                        <div class="col-md-6">
                            <label for="">Your key survivor:</label>
                            <input type="text" name="survivor-input" v-model="survivor" class="form-control" required/>
                        </div>
                        <div class="col-md-6">
                            <label for="">Key of Trader:</label>
                            <input type="text" name="trader-input" v-model="trader" class="form-control" required/>
                    </div>
                    </div>

                    <hr>
                    <button name="verify_survivors" class="btn btn-default">Start Transition</button>
                </form>
            </div>
        </div>
    </div>
</template>
<script>

	/* eslint-disable no-undef  */
	/* eslint-disable no-underscore-dangle */

import _ from 'lodash';
import Promise from 'bluebird';
import SurvivorService from '../../domain/survivor/SurvivorService';

export default {
		data() {
			return {
				survivor: '',
				trader: '',
			};
		},
		methods: {
			checkIdForTrade() {
				const vm = this;

				const checkTrader = () => new Promise((resolve, reject) => {
					vm.service.getSurvivorById(vm.trader)
                                .then(res => resolve(res))
                                .catch(err => reject(err));
				});

				const checkSurvivor = () => new Promise((resolve, reject) => {
					vm.service.getSurvivorById(vm.survivor)
                                     .then(res => resolve(res))
                                     .catch(err => reject(err));
				});

				Promise.props({
					survivor: checkSurvivor(),
					trader: checkTrader(),
				}).then((result) => {
					toastr.success('Access keys checked', 'Status');
					vm.$router.push({ name: 'trade',
						params: {
							id1: _.map(result.survivor, x => x._id),
							id2: _.map(result.trader, x => x._id),
						} });
				})
                .catch((err) => {
		toastr.error(`Error in Access keys checked => ${err}`, 'Status');
});
			},
		},

		created() {
			const vm = this;
			vm.service = new SurvivorService(vm.$resource);
		},

};


</script>
<style>
</style>



