<template>
    <div class="container">
  		<div class="row">


            <div class="col-md-7 col-md-offset-2" >

                <h3 class="page-header">
                <router-link :to="{ name: 'home'}"> HOME </router-link> < Search a Survivor</h3>
                <form>
                <label for="">Type name of survivor: </label>
                <input type="search"
										name="search_form"
										class="form-control"
										@input="filter = $event.target.value"
										placeholder="Search...">

                <table class="table table-striped table-hover ">
                    <thead>
                        <tr>
                        <th>Name</th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th>Possible Actions</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr  v-for="s of filterSurvivors" v-if="filter != ''">


                                <td>{{s.name}}</td>
                                <td></td>
                                <td></td>
                                <td></td>
                                <td>
                                    <router-link :to="{ name: 'preview', params:{id:s._id} }">
                                        <button name="profile_survivor" class="btn btn-default">Profile</button>
                                    </router-link>
                                    <router-link :to="{ name: 'report_infected', params:{id:s._id} }">
                                        <button name="report_survivor" class="btn btn-default">Infected</button>
                                    </router-link>

                                </td>
                        </tr>
                    </tbody>
                </table>

                </form>

            </div>
        </div>
    </div>

</template>
<script>
	/* eslint-disable no-undef  */
import SurvivorService from '../../domain/survivor/SurvivorService';

export default {
		data() {
			return {
				survivors: [],
				filter: '',
			};
		},

		computed: {
			filterSurvivors() {
				const vm = this;
				if (vm.filter) {
					const exp = new RegExp(vm.filter.trim(), 'i');
					return vm.survivors.filter(s => exp.test(s.name));
				}
				return vm.survivors;
			},
		},

		created() {
			const vm = this;
			vm.service = new SurvivorService(vm.$resource);

			vm.service
					.listSurvivor()
					.then((res) => {
						vm.survivors = res;
					})
					.catch(err => toastr.error(`Error in load list: ${err}`, 'Status'));
		},
	};
</script>
<style>
</style>



