<template>
    <div class="container">
  		<div class="row">
            <div class="col-md-7 col-md-offset-2" >
                <h3 class="page-header"> 
                <router-link :to="{ name: 'home'}"> HOME </router-link> < Edit Survivor</h3>
                <form @submit.prevent="verifySurvivor()">
                    Key for edit survivor: <input class="form-control" type="text" v-model="objectId" required>
                    <hr>
                   <button class="btn btn-default" name="search_survivor">Search</button>
                </form>
            </div>
        </div>    
    </div>
</template>
<script>
	/* eslint-disable no-undef  */
	import SurvivorService from '../../domain/survivor/SurvivorService';
	import Survivor from '../../domain/survivor/Survivor';

export default {
		data() {
			return {
				objectId: '',
			};
		},
		methods: {

			verifySurvivor() {
				const vm = this;
	
				vm.service
				.getSurvivorById(vm.objectId)
				.then((res) => {
					toastr.success('Suvivor save with success', 'Status');
					vm.$router.push({ name: 'edit_survivor', params: { id: vm.objectId } });
					vm.objectId = '';
				})
				.catch(err => toastr.error(`Error in get data about survivor: ${err}`, 'Status'));
			},

		},
		created() {
			const vm = this;
			vm.service = new SurvivorService(vm.$resource);
		},
	};
</script>
<style>
</style>



