export default class SurvivorService {

	constructor(resource) {
		const vm = this;
		vm._resource = resource;
	}

	saveSurvivor(survivor) {
		const vm = this;
		return vm._resource('api/survivor{/id}').save(survivor);
	}

	updateSurvivor(id, survivor) {
		const vm = this;
		return vm._resource('api/survivor{/id}').update({ id }, survivor);
	}

	listSurvivor() {
		const vm = this;
		return vm._resource('api/survivor{/id}')
		.query()
		.then(res => res.json());
	}

	getSurvivorById(id) {
		const vm = this;
		return vm._resource('api/survivor{/id}')
		.get({ id })
		.then(res => res.json());
	}

	reportSurvivorInfected(obj) {
		const vm = this;
		return vm._resource('api/survivor/report_survivor_infected{/id}')
		.save(obj);
	}

	tradeBtwSurvivorAndTraders(obj) {
		const vm = this;
		return vm._resource('api/survivor/trade_between_survivor')
				.save(obj);
	}

	reportPercenteOfSurvivors() {
		const vm = this;
		return vm._resource('api/report/percenteofsurvivors')
		.query()
		.then(res => res.json());
	}

	reportPointsForResources() {
		const vm = this;
		return vm._resource('api/report/pointsforresources')
		.query()
		.then(res => res.json());
	}

	reportMediaOfResources() {
		const vm = this;
		return vm._resource('api/report/mediaofresources')
		.query()
		.then(res => res.json());
	}
}



