<template>
    <div class="container">
  		<div class="row">
            <div class="col-md-7 col-md-offset-2" >
                <h3 class="page-header">
                <router-link :to="{ name: 'home'}"> HOME </router-link> <  Reports</h3>
                <div class="row">

                    <div class="col-md-6">
                    <label>Percentage of survivors infected and non infected.</label>
                        <doughnut-chart
                            :data="percent"
                            :labels="title"
                            :backgrounds="backgrounds1">

                        </doughnut-chart>
                    </div>
                    <div class="col-md-6">
                        <label>Average amount of each kind of resource by survivor.</label>
                            <doughnut-chart
                                :data="media"
                                :labels="title2"
                                :backgrounds="backgrounds2">
                            </doughnut-chart>
                        </div>
                    </div>
                    <hr>
                    <div class="row">
                        <div class="col-md-6">
                            <label>Points lost because of infected survivor.</label>
                            <doughnut-chart
                                :data="points"
                                :labels="title3"
                                :backgrounds="backgrounds3">

                            </doughnut-chart>
                        </div>
                    </div>

                </div>
            </div>
        </div>
</template>
<script>
/* eslint-disable no-undef  */
/* eslint-disable no-underscore-dangle */

import SurvivorService from '../../domain/survivor/SurvivorService';
import DoughnutChart from '../shared/charts/DoughnutChart';


export default {

	data() {
		return {
			percent: [],
			media: [],
			points: [],
			title: [],
			title2: [],
			title3: [],
			backgrounds1: [],
			backgrounds2: [],
			backgrounds3: [],
		};
	},
	components: {
		DoughnutChart,
	},

	created() {
		const vm = this;
		vm.service = new SurvivorService(vm.$resource);

		vm.service
            .reportPercenteOfSurvivors()
            .then(res => _.map(res, (x) => {
	vm.percent.push(x.qtd);
	vm.title.push(x.type);
	vm.backgrounds1.push(x.bkg);
}))
            .catch((err) => {
	toastr.error(`Error in load this chart: ${err}`, 'Status');
});

		vm.service
            .reportMediaOfResources()
            .then(res => _.map(res, (x) => {
	vm.media.push(x.qtd);
	vm.title2.push(x.name);
	vm.backgrounds2.push(x.bkg);
}))
             .catch((err) => {
	toastr.error(`Error in load this chart: ${err}`, 'Status');
});

		vm.service
            .reportPointsForResources()
            .then(res => _.map(res, (x) => {
	vm.points.push(x.qtd);
	vm.title3.push(x.name);
	vm.backgrounds3.push(x.bkg);
}))
            .catch((err) => {
	toastr.error(`Error in load this chart: ${err}`, 'Status');
});
	},
};
</script>
<style>
</style>



