<template>
     <div class="container">
  		<div class="row">
            <div class="col-md-7 col-md-offset-2" >
                <h3 class="page-header">
                <router-link :to="{ name: 'home'}"> HOME </router-link> <  Edit Position</h3>
                    <form @submit.prevent="updateSurvivor()" >
                    <h3 class="name-edit-survivor">
											Name: {{survivor.name}}
										</h3>
                    <hr>
                    <div class="well">
                        <gmap-map
                        :center="center"
                        :zoom="7"
                        style="width: 100%; height: 300px"
                        >
                        <gmap-marker
                                :key="index"
                                v-for="(m, index) in markers"
                                :position="m.lastLocation"
                                :draggable="true"
                                @position_changed="updMarker(m, $event)"
                                @dragend="logMarker(m)"
                            ></gmap-marker>
                        </gmap-map>
                    </div>
                    <input type="submit" class="btn  btn-primary btn-lg btn-block" value="Update">
                </form>
            </div>
        </div>
	</div>
</template>
<script>
/* eslint-disable no-undef  */
/* eslint-disable no-param-reassign*/
import _ from 'lodash';
import SurvivorService from '../../domain/survivor/SurvivorService';
import Survivor from '../../domain/survivor/Survivor';


export default {
	data() {
		return {
			id: this.$route.params.id,
			survivor: new Survivor(),
			position: {},
			markers: [],
			center: { lat: -19.8934596, lng: -44.0586543 },
		};
	},
	methods: {

		updateSurvivor() {
			const vm = this;
			let updateSurvivor;

			if (!_.isEmpty(vm.position)) updateSurvivor = _.merge(vm.survivor, vm.position);
			else updateSurvivor = _.merge(vm.survivor, vm.center);
			vm.service
                .updateSurvivor(vm.id, updateSurvivor)
                .then(() => {
	toastr.success('Suvivor update with success', 'Status');
	vm.$router.push({ name: 'home' });
})
                .catch((err) => {
	toastr.error(`Error in update location: ${err}`, 'Status');
});
		},

		updMarker(m, event) {
			m.position = {
				lat: event.lat(),
				lng: event.lng(),
			};
		},

		logMarker(m) {
			const vm = this;
			vm.position = m.position;
		},
	},
	created() {
		const vm = this;
		vm.service = new SurvivorService(vm.$resource);
		vm.service
        .getSurvivorById(vm.id)
        .then(res => _.map(res, (x) => {
	vm.markers.push(x);
	vm.survivor = {
		name: x.name,
		age: x.age,
		gender: x.gender,
		water: x.inventory.water,
		ammunition: x.inventory.ammunition,
		food: x.inventory.food,
		medication: x.inventory.medication,
	};
}))
        .catch((err) => {
	toastr.error(`Error in get location about this survivor: ${err}`, 'Status');
});
	},
};

</script>

<style>
</style>



