import Survivor from './components/survivor/SignupSurvivor';
import SurvivorId from './components/survivor/SurvivorID';
import EditSurvivor from './components/survivor/EditSurvivor';
import EditSurvivorPath from './components/survivor/EditSurvivorPath';
import SurvivorsMap from './components/survivor/SurvivorsMap';
import SearchSurvivor from './components/survivor/SearchSurvivor';
import PreviewOfSurvivor from './components/survivor/PreviewOfSurvivor';
import ReportAInfectedSurvivor from './components/survivor/ReportAInfectedSurvivor';
import TradeBtwSurvivors from './components/survivor/TradeBtwSurvivors';
import CheckSurvivorsForTrade from './components/survivor/CheckSurvivorsForTrade';
import ReportsAboutSurvivors from './components/survivor/ReportsAboutSurvivors';
import Home from './components/home/Home';

const routes = [
    { path: '', name: 'home', component: Home, title: 'Home' },
    { path: '/signup', name: 'signup', component: Survivor, title: 'Register Survivor' },
    { path: '/signup_id', name: 'signup_id', component: SurvivorId, title: 'Register Survivor' },
    { path: '/edit_path', name: 'edit_path_survivor', component: EditSurvivorPath, title: 'Edit Survivor' },
		{ path: '/edit_survivor', name: 'edit_survivor', component: EditSurvivor, title:	'Edit' },
    { path: '/map', name: 'map', component: SurvivorsMap, title: 'Map of Survivors' },
    { path: '/search_survivor', name: 'search', component: SearchSurvivor, title: 'Search Survivor' },
    { path: '/preview_of_survivor', name:	'preview', component: PreviewOfSurvivor, title:	'Preview of Survivor' },
		{ path: '/report_infected', name:	'report_infected', component: ReportAInfectedSurvivor },
		{ path: '/verify_survivors', name:	'verify_survivors', component: CheckSurvivorsForTrade, title:	'Trade' },
    { path: '/trade', name:	'trade', component: TradeBtwSurvivors },
    { path: '/report', name:	'report', component: ReportsAboutSurvivors, title:	'Reports' },
    { path: '*', component: Home },
];

export default routes;



