import { Doughnut } from 'vue-chartjs';

export default Doughnut.extend({
	props: ['data', 'labels', 'backgrounds'],
	mounted() {
		this.renderChart({
			labels: this.labels,
			datasets: [
				{
					backgroundColor: this.backgrounds,
					data: this.data,
				},
			],
		});
	},
	watch: {
		data() {
			this._chart.destroy();
			this.renderChart({
				labels: this.labels,
				datasets: [
					{
						backgroundColor: this.backgrounds,
						data: this.data,
					},
				],
			});
		},
	},
});



