<template>
    <div class="container">
  		<div class="row">
    		<div class="col-md-7 col-md-offset-2" >
			<h3 class="page-header">
				<router-link :to="{ name: 'home'}"> HOME </router-link> <  Register Survivor
			</h3>
			<form @submit.prevent="createSurvivor()" >
					<div class="form-group row">
						<div class="col-sm-8" >
							<label for="">Name:</label>
							<input class="form-control"
							name="name_survivor"
							v-model="survivor.name"
							type="text" placeholder="name..."
							required>
						</div>
						<div class="col-sm-2">
							<label for="">Age:</label>
							<input class="form-control"
							v-model="survivor.age"
							name="age_survivor"
							type="number"
							placeholder="age..."
							required>
						</div>
						<div class="col-sm-2">
							<label for="">Gender:</label>
							<select  class="form-control" name="gender_survivor" id="" v-model="survivor.gender" required>
								<option value="M">M</option>
								<option value="F">F</option>
							</select>
						</div>
					</div>
					<div class="form-group row">
						<div class="col-sm-3">
							<label for="">Water:</label>
							<input class="form-control" v-model="survivor.water" type="number" name="water_survivor" placeholder="amount..." required min="0">
						</div>
						<div class="col-sm-3">
							<label for="">Food:</label>
							<input class="form-control" v-model="survivor.food" type="number" name="food_survivor" placeholder="amount..." required min="0">
						</div>
						<div class="col-sm-3">
							<label for="">Ammunition:</label>
							<input class="form-control" v-model="survivor.ammunition" type="number" name="ammunition_survivor" placeholder="amount..." required min="0">
						</div>
						<div class="col-sm-3">
							<label for="">Medication:</label>
							<input class="form-control" v-model="survivor.medication" type="number" name="medication_survivor" placeholder="amount..." required min="0">
						</div>
					</div>
					<hr>
					<h4>Please, make your location here:</h4>
					<div class="well">
						<gmap-map
								:center="center"
								:zoom="7"
								style="width: 100%; height: 300px"
							>
						<gmap-marker
							:key="index"
							v-for="(m, index) in markers"
							:position="m.position"
							:draggable="true"
							@position_changed="updMarker(m, $event)"
							@dragend="logMarker(m)"
						></gmap-marker>
						</gmap-map>
					</div>
					<input type="submit" class="btn  btn-primary btn-lg btn-block" name="signup_survivor" value="Save">
			</form>
			<br>
		</div>
	</div>
</div>
</template>
<script>
/* eslint-disable no-undef  */
/* eslint-disable no-param-reassign*/
/* eslint-disable no-underscore-dangle */
import _ from 'lodash';
import SurvivorService from '../../domain/survivor/SurvivorService';
import Survivor from '../../domain/survivor/Survivor';

export default {

	data() {
		return {
			survivor: new Survivor(),
			resources: [],
			resource: {
				item: '',
				amount: 0,
			},
			position: {},
			markers: [{ position: { lat: -19.8934596, lng: -44.0586543 } }],
			center: { lat: -19.8934596, lng: -44.0586543 },
		};
	},
	methods: {

		createSurvivor() {
			const vm = this;
			let newSurvivor;

			if (!_.isEmpty(vm.position)) newSurvivor = _.merge(vm.survivor, vm.position);
			else newSurvivor = _.merge(vm.survivor, vm.center);

			vm.service
				.saveSurvivor(newSurvivor)
				.then((res) => {
					toastr.success('Suvivor save with success', 'Status');
					vm.$router.push({ name: 'signup_id', params: { id: res.body._id } });
					vm.survivor = {};
				})
				.catch(err => toastr.error(`Error in signup survivor: ${err}`, 'Status'));
		},

		updMarker(m, event) {
			m.position = {
				lat: event.lat(),
				lng: event.lng(),
			};
		},
		logMarker(m) {
			const vm = this;
			vm.position = m.position;
		},
	},
	created() {
		const vm = this;
		vm.service = new SurvivorService(vm.$resource);
	},
};

</script>

<style>



</style>



