<template>
	 <div class="container">
  		<div class="row">


			<div class="col-md-7 col-md-offset-2" >

				<h3 class="page-header">
				<router-link :to="{ name: 'home'}"> HOME </router-link> <  Profile Survivor</h3>
					<p>Key: {{survivor.id}}</p>
				<hr>
					<p name="survivor_name">Name: {{survivor.name}}</p>
				<hr>
					<p>Age: {{survivor.age}}</p>
				<hr>
					<h3>Inventory</h3>
					<p>Water: {{survivor.water}}</p>
					<p>Food: {{survivor.food}}</p>
					<p>Ammunition: {{survivor.ammunition}}</p>
					<p>Medication: {{survivor.medication}}</p>
				<hr>
						<gmap-map
							:center="center"
							:zoom="7"
							style="width: 100%; height: 300px"
						>
							<gmap-marker
								:position="survivor.lastLocation"
							></gmap-marker>
						</gmap-map>
			</div>
		</div>
	</div>

</template>
<script>
	/* eslint-disable no-undef  */
	/* eslint-disable no-param-reassign*/
	/* eslint-disable no-underscore-dangle */
	/* eslint-disable  no-return-assign */
	import _ from 'lodash';
	import SurvivorService from '../../domain/survivor/SurvivorService';
	import Survivor from '../../domain/survivor/Survivor';

	export default {
		data() {
			return {
				center: { lat: -19.8934596, lng: -44.0586543 },
				survivor: new Survivor(),
				id: this.$route.params.id,
			};
		},
		created() {
			const vm = this;

			vm.service = new SurvivorService(vm.$resource);

			if (vm.id) {
				vm.service
			.getSurvivorById(vm.id)
			.then(survivor => _.map(survivor, x => vm.survivor = {
				id: x._id,
				name: x.name,
				age: x.age,
				gender: x.gender,
				water: x.inventory.water,
				ammunition: x.inventory.ammunition,
				food: x.inventory.food,
				medication: x.inventory.medication,
				lastLocation: x.lastLocation,
			}))
				.catch((err) => {
					toastr.error(`Error in get data's about this survivor: ${err}`, 'Status');
				});
			}
		},
	};

</script>
<style>
</style>



