<template>
    <div class="container">
  		<div class="row">
			<div class="col-md-12">
                <h3 class="page-header">
			    <router-link :to="{ name: 'home'}"> HOME </router-link> <  Trade Between Survivors</h3>
                    <div class="row">
                        <form @submit.prevent="tradeNow()">
                            <div class="row">
                                <div class="col-md-4">
                                <h2>Survivor:</h2>
                                <hr>
                                    <h5>Name: {{survivor.name}}</h5>
                                    <hr>
                                    <h5>Age: {{survivor.age}}</h5>
                                    <hr>
                                    <h5>Gender: {{survivor.gender}}</h5>
                                <hr>
                                    <div class="well">
                                        <h3>Water: {{survivor.water}}</h3>
                                        <h3>Food: {{survivor.food}}</h3>
                                        <h3>Ammunition: {{survivor.ammunition}}</h3>
                                        <h3>Medication: {{survivor.medication}}</h3>
                                    </div>
                                <hr>

                                <div class="form-group row">
                                    <div class="col-sm-3">
                                        <label for="">Water:</label>
                                        <input class="form-control" name="survivor_water" v-model="tradeF.water" type="number" min="0" >
                                    </div>
                                    <div class="col-sm-3">
                                        <label for="">Food:</label>
                                        <input class="form-control"	name="survivor_food" v-model="tradeF.food" type="number" min="0" >
                                    </div>
                                    <div class="col-sm-3">
                                        <label for="">Ammunition:</label>
                                        <input class="form-control"	name="survivor_ammunition" v-model="tradeF.ammunition" type="number" min="0" >
                                    </div>
                                    <div class="col-sm-3">
                                        <label for="">Medication:</label>
                                        <input class="form-control"	name="survivor_medication" v-model="tradeF.medication" type="number" min="0" >
                                    </div>
                                </div>
                            </div>
                            <div class="col-md-4 col-md-offset-3">
                                <h2>Trader:</h2>
                                <hr>
                                    <h5>Name: {{trader.name}}</h5>
                                    <hr>
                                    <h5>Age: {{trader.age}}</h5>
                                    <hr>
                                    <h5>Gender: {{trader.gender}}</h5>

                                <hr>
                                    <div class="well">
                                        <h3>Water: {{trader.water}}</h3>
                                        <h3>Food: {{trader.food}}</h3>
                                        <h3>Ammunition: {{trader.ammunition}}</h3>
                                        <h3>Medication: {{trader.medication}}</h3>
                                    </div>
                                <hr>
                                 <div class="form-group row">
                                    <div class="col-sm-3">
                                        <label for="">Water:</label>
                                        <input class="form-control" name="trader_water" v-model="tradeS.water" type="number" min="0" >
                                    </div>
                                    <div class="col-sm-3">
                                        <label for="">Food:</label>
                                        <input class="form-control" name="trader_food"	v-model="tradeS.food" type="number" min="0" >
                                    </div>
                                    <div class="col-sm-3">
                                        <label for="">Ammunition:</label>
                                        <input class="form-control" name="trader_ammunition" v-model="tradeS.ammunition" type="number" min="0" >
                                    </div>
                                    <div class="col-sm-3">
                                        <label for="">Medication:</label>
                                        <input class="form-control"	name="trader_medication"	v-model="tradeS.medication" type="number" min="0" >
                                    </div>
                                </div>
                            </div>


                            </div>

                           <div class="row">
                           <hr>
                                <div class="col-md-7 col-md-offset-2" >
                                    <input type="submit"
                                    class="btn  btn-primary btn-lg btn-block"
																		name="make_trade"				
                                    value="Save">
                                </div>
                           </div>
                        </form>
                    </div>
			</div>
		</div>
    </div>
</template>
<script>
	/* eslint-disable no-undef  */
	/* eslint-disable no-underscore-dangle */
	/* eslint-disable  no-return-assign */
	import _ from 'lodash';
import SurvivorService from '../../domain/survivor/SurvivorService';
import Survivor from '../../domain/survivor/Survivor';


export default {
		data() {
			return {
				survivorId: this.$route.params.id1,
				traderId: this.$route.params.id2,
				survivor: new Survivor(),
				trader: new Survivor(),
				tradeF: new Survivor(),
				tradeS: new Survivor(),
			};
		},
		methods: {

			tradeNow() {
				const vm = this;

				const transform = value => parseInt(value);
				const data = {
					survivorId: vm.survivorId[0],
					traderId: vm.traderId[0],
					survivorOffered: {
						water: transform(vm.tradeF.water),
						food: transform(vm.tradeF.food),
						ammunition: transform(vm.tradeF.ammunition),
						medication: transform(vm.tradeF.medication),
					},
					traderOffered: {
						water: transform(vm.tradeS.water),
						food: transform(vm.tradeS.food),
						ammunition: transform(vm.tradeS.ammunition),
						medication: transform(vm.tradeS.medication),
					},
				};
				vm.service
			.tradeBtwSurvivorAndTraders(data)
			.then(() => {
				toastr.success('Trade complete', 'Status');
				vm.$router.push({ name: 'home' });
			})
					.catch((err) => {
						toastr.error(`Error in trade: ${err}`, 'Status');
					});
			},

		},
		created() {
			const vm = this;
			vm.service = new SurvivorService(vm.$resource);
			vm.service
		.getSurvivorById(vm.survivorId)
		.then(res => _.map(res, (x) => {
			vm.survivor = {
				name: x.name,
				age: x.age,
				gender: x.gender,
				water: x.inventory.water,
				ammunition: x.inventory.ammunition,
				food: x.inventory.food,
				medication: x.inventory.medication,
			};
		}))
			.catch((err) => {
				toastr.error(`Error in get survivor: ${err}`, 'Status');
			});

			vm.service2 = new SurvivorService(vm.$resource);
			vm.service2
		.getSurvivorById(vm.traderId)
		.then(res => _.map(res, x =>
			vm.trader = {
				name: x.name,
				age: x.age,
				gender: x.gender,
				water: x.inventory.water,
				ammunition: x.inventory.ammunition,
				food: x.inventory.food,
				medication: x.inventory.medication,
			},
		))
		.catch((err) => {
			toastr.error(`Error in get survivor: ${err}`, 'Status');
		});
		},
};
</script>
<style>
</style>



