<template>
    <div class="container">
  		<div class="row">
			<div class="col-md-7 col-md-offset-2" >
					<div class="jumbotron">
						<h3>Report Survivor:</h3>
                        *<p>Be aware that this can harm a survivor if he or she is not infected</p>
						<br>
                        <label for="">Enter your name:</label>
                        <input type="text" class="form-control" v-model="obj.name" required>
						<hr>

						<button name="report" class="btn btn-default" v-on:click.prevent="reportAInfectedSurvivor(id,obj)">Report</button>
						<router-link :to="{ name: 'search' }">
                            <button class="btn btn-default">Back</button>
                        </router-link>

					</div>
			</div>
		</div>
    </div>
</template>
<script>

/* eslint-disable no-undef  */

import _ from 'lodash';
import SurvivorService from '../../domain/survivor/SurvivorService';

export default {
	data() {
		return {
			id: this.$route.params.id,
			obj: {
				name: '',
				date: new Date(),
			},
		};
	},
	methods: {
		reportAInfectedSurvivor(id, obj) {
			const vm = this;
			const obj1 = {
				id,
			};
			vm.service
					.reportSurvivorInfected(_.merge(obj, obj1))
					.then(() => {
						toastr.info('Report Survivor', 'Status');
						vm.$router.push({ name: 'home' });
					})
					.catch((err) => {
						toastr.error(`Error in report Survivor: ${err}`, 'Status');
					});
		},
	},
	created() {
		const vm = this;
		vm.service = new SurvivorService(vm.$resource);
	},

};
</script>
<style>
</style>



